import Header from './detail/header'
import Detail from './detail/detail'
import axios from 'axios'

export default {
  name: 'detail',
  components: {
    Header,
    Detail
  },
  data () {
    return {
      mission: [],
      name: ''
    }
  },
  methods: {
    getMission () {
      sessionStorage.setItem('mission_id', this.$route.params.miId)
      axios.get(this.url + '/Mission/id/' + this.$route.params.miId)
        .then(this.getMissionSucc)
    },
    getMissionSucc (res) {
      if (res.status === 202) this.$router.push('/noProject')
      else if (res) {
        res = res.data
        this.mission = res
        this.name = res.name
      }
    }
  },
  mounted () {
    this.getMission()
  }
}



